
<template>
    <div class="pages-tables " id="pages-tables">
        <div class="textarea">
            <h1>复杂表头 json 数据验证：</h1>
            <p>请输入表头 json 数据：</p>
            <p class="message">提示：输入json覆盖原来的即可，离开焦点就可自动变化表头，且有json结构验证是否正确的功能</p>
            <textarea v-model="header"  @change="change">我是通过cols和rows定的大小，我的cols是20，rows是10</textarea>
        </div>
        <div class="waterMask" id="waterMark" :style="{height: theadHeight + 'px', top: offsetHeight + 'px'}"></div>
        <h1>表头展示效果如下：</h1>
        <div class="rolling-table auto-table" ref="tableBox" :style="{height: maxHeight + 'px'}">
            <table class="table" id="table" cellpadding="0" cellspacing="0" ref="rollingTable">
                <thead ref="thead">
                    <tr v-for="(x,i) in headerList" :key="i">
                        <th class="rows " :class="{'cross': index == 0 && i == 0}" v-for="(l,index) in x" :key="index" :colspan="l.width" :rowspan="l.height">{{l.name}}</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(b,i) in bodyList" :key="i + 'a'">
                        <template v-for="(x, index) in b">
                            <td :class="{'cols':  index == 0 }" :key="index + 'b'">
                                {{ x | valueFromt }}
                            </td>
                        </template>
                    </tr>
                    <tr></tr>
                </tbody>

            </table>
        </div>
    </div>
</template>
<script>
// 说明这个 demo 是给 pc 端用的，单位要为 px
import { Field } from 'mint-ui';
import { createIScrollerAuto, refreshScroller } from "libs/iscrollTable";
import { addWaterMarker, formatArray, getLeafCountTree, jsonFromat } from "libs/common/common";
import commonMixin from 'mixins/common/common.js';
import { Toast } from 'mint-ui';
export default {
    mixins: [commonMixin],
    data() {
        return {
            header: '',
            maxHeight: '100%',
            theadHeight: '100%',
            offsetHeight: 0,
            scroll: {
                scroller: null
            },
            headerList: [],
            headerList1: [
                [
                    {
                        name: "第一行合并3行1列22",
                        width: 1, //指定单元格 横向 跨越的 列数
                        height: 3, //指定单元格 纵向 跨越的 行数
                    },
                    {
                        name: "第一行合并2列",
                        width: 3,
                        height: 1,
                    },
                    {
                        name: "第一行合并2列",
                        width: 2,
                        height: 1,
                    },
                    {
                        name: "第一行合并3列",
                        width: 3,
                        height: 1,
                    },
                ],
                [
                    {
                        name: "第二行日期",
                        width: 1, //指定单元格 横向 跨越的 列数
                        height: 1, //指定单元格 纵向 跨越的 行数
                    },
                    {
                        name: "第二行日期合并2列",
                        width: 2,
                        height: 1,
                    },
                    {
                        name: "第二行日期合并2列",
                        width: 2,
                        height: 1,
                    },
                    {
                        name: "第二行日期合并3列",
                        width: 3,
                        height: 1,
                    },
                ],
                [
                    {
                        name: "第三行当月新增",
                        width: 1,  //指定单元格 横向 跨越的 列数
                        height: 1, //指定单元格 纵向 跨越的 行数
                    },
                    {
                        name: "第三行当月新增1第三行",
                        width: 1,
                        height: 1,
                    },
                    {
                        name: "第三行当月新增2",
                        width: 1,
                        height: 1,
                    },
                    {
                        name: "第三行当月新增3第三行当月",
                        width: 1,
                        height: 1,
                    },
                    {
                        name: "第三行当月新增4",
                        width: 1,
                        height: 1,
                    },
                    {
                        name: "第三行当月新增5",
                        width: 1,
                        height: 1,
                    },
                    {
                        name: "第三行当月新增6",
                        width: 1,
                        height: 1,
                    },
                ],
            ],
            headerFieldList: ['statis_date', 'area_name', "area_name1", "area_name2", "area_name3", "area_name4", "area_name5", "area_name6",],
            bodyList: [],

        }
    },
    filters: {
        valueFromt: function (value) {
            let realValue = ''
            if (!value) return ''
            value = value.toString()
            if (value.length > 20) {
                realValue = value.slice(0, 15) + '...'
            } else {
                realValue = value
            }
            return realValue
        },
    },
    methods: {
        change() {
            try {
                // this.header 
                // let headerData = jsonFromat(JSON.stringify(JSON.parse(this.header)))
                // console.log("this.header:",headerData)
                const newData = formatArray(JSON.parse(this.header) , 0)
                console.log(newData);

                let maxLevel = newData[newData.length - 1].level

                console.log("setHeight", this.setHeight(newData, maxLevel + 1))

                console.log('sortAray', this.arayLayered(newData, maxLevel))

                this.headerList = this.arayLayered(newData, maxLevel)

            } catch (e) {
                // typeof(e)
                this.errorContent = JSON.stringify(e)
                console.log('typeof(e):', typeof(e))
                console.log('e:', e)
                Toast('又写错了？撸太多了！！请效验json格式是否正确！！');
            }

        },
        setHeight(arr, maxLevel) {
            console.log("setHeight maxLevel", maxLevel)
            for (let i = maxLevel; i >= 0; i--) {
                for (let j = 0; j < arr.length; j++) {
                    // 设置高
                    if (arr[j].childrenNumber) {
                        arr[j].height = 1
                    } else {
                        arr[j].height = maxLevel - arr[j].level
                    }
                }
            }
            return arr
        },
        arayLayered(arr, maxLevel) {
            let returnArr = []
            for (let i = 0; i <= maxLevel; i++) {
                let arrLevel = []
                for (let j = 0; j < arr.length; j++) {
                    if (arr[j].level == i) {
                        arrLevel.push(arr[j])
                    }
                }
                returnArr[i] = arrLevel
            }
            return returnArr
        }
    },
    mounted() {
        let bodyListA = [
            ["地区最先", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
            ["地区最后", "数据111", "数据112", "数据121", "数据122", "数据131", "数据132", "数据14"],
        ]
        // for (let i = 0; i < 1; i++) {
        //     this.bodyList = this.bodyList.concat(bodyListA)
        // }
        // console.log('this.bodyList', this.bodyList)
        // this.maxHeight = window.screen.height - (window.screen.height - window.innerHeight)
        // this.scroll.scroller = createIScrollerAuto(".auto-table");

        const data = [
            {
                name: '地区',
            },
            {
                name: '总数据',
                children: [
                    {
                        name: '数据1',
                        children: [
                            {
                                name: '数据11',
                                children: [{
                                    id: 5,
                                    pid: 4,
                                    name: '数据111',
                                },
                                {
                                    name: '数据112',
                                }
                                ]
                            },
                            {
                                name: '数据12',
                                children: [{
                                    name: '数据121',
                                },
                                {
                                    name: '数据122',
                                }
                                ]
                            },
                            {
                                name: '数据13',
                                children: [{
                                    name: '数据131',
                                },
                                {
                                    name: '数据132',
                                }
                                ]
                            },
                            {
                                name: '数据14',
                            },
                        ]
                    }
                ]
            }
        ];
        this.header = jsonFromat(JSON.stringify(data))

        this.change()

        // setTimeout(() => {
        //     this.offsetHeight = this.$refs.thead.offsetHeight - 50
        //     this.theadHeight = window.screen.height - this.offsetHeight
        //     // console.log("this.$refs.thead.offsetHeight",this.$refs.thead.offsetHeight)
        //     // addWaterMarker(document.getElementById('waterMark'), "2018-07-30 12:00:00 xuyingbiaochen", 700, 200)
        // }, 500)

        this.setTitle({ 'title': '复杂表头 json 数据验证' });

    }
}

</script>
<style lang="less" scoped>
.textarea{
    font-size: 20px;
    textarea{
        border: 1px solid #eee;
        font-size: 16px;
        resize: both;
        width: 800px;
        min-height: 900px;
    }
    .message{
        color: red;
        font-size: 16px;
    }
}
.waterMask {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 4;
    pointer-events: none;
}
.pages-tables {
  -webkit-overflow-scrolling: touch; // ios滑动顺畅
  position: relative;
  margin-left: 5%;
  padding-bottom: 160px;
  margin: 0 50px;
}
.rolling-table {
    height: 100%;
    font-size: 0.28rem;
    color: #86939a;
    background-color: #fff;
    width: 100%;
    -webkit-overflow-scrolling: touch;
    position: relative;
    top: 0;
    // overflow: hidden;
  }
.rows {
    position: relative;
    z-index: 3;
}
.cross {
    position: relative;
    z-index: 5;
}
table td {
  border: 0px solid #000;
  font-size: 25px;
  background: #fff;
}
::-webkit-scrollbar {
    display: none;
}
.table {
  border-collapse: collapse; //去掉重复的border
  color: #86939e;
  font-size: 25px;
  border: 0px solid #000;
  min-height: 100%;
  text-align: center;
  td {
    border-bottom: 1px solid #eee;
    height: 30px;
    line-height: 30px;
    padding: 0 0.2rem;
    // white-space: nowrap;
    white-space: inherit;
    max-width: 500px;
    min-width: 50px;
    // overflow:hidden; 
    // text-overflow:ellipsis;
    // -webkit-line-clamp:2; 
  }
  th {
    color: #43484d;
    white-space: nowrap;
    // white-space: inherit;
    height: 30px;
    line-height: 30px;
    padding: 0rem 16px;
    background-color: #f3f4f6;
    font-weight: normal;
    padding-bottom: 0;
    padding-top: 0;
    border: 1px solid red;
    &:last-child{
        // border-right: 0rem solid #e4e8f5;
    }
  }
}
tr{
    position: relative;
    background-color: #fff;
    &:nth-of-type(odd){
        td{
            background-color: #ebf9fc;
        }
    }
}
</style>



